import React, { useState } from "react";
import "./WorkoutPlans.css";

function WorkoutPlans() {
  const [selectedWorkout, setSelectedWorkout] = useState(null);

  const workoutPlans = {
    muscleGain: {
      title: " Muscle Gain",
      description: "Increase muscle mass with this intense strength-building routine.",
      weeklyPlan: [
        { day: "Monday", exercises: [" Bench Press - 4x8", " Squats - 4x10", " Deadlifts - 4x6", " Shoulder Press - 3x10", " Bicep Curls - 3x12"] },
        { day: "Tuesday", exercises: [" Pull-ups - 3x10", " Barbell Rows - 4x8", " Lat Pulldowns - 4x8", " Dumbbell Curls - 3x12", " Shrugs - 3x15"] },
        { day: "Wednesday", exercises: [" Cardio - 30 mins", " Stretching & Mobility Work"] },
        { day: "Thursday", exercises: [" Squats - 4x10", " Deadlifts - 3x6", " Leg Press - 4x10", " Lunges - 3x12", " Calf Raises - 3x20"] },
        { day: "Friday", exercises: [" Bench Press - 4x8", " Dumbbell Flys - 3x12", " Tricep Dips - 3x15", " Overhead Triceps Extension - 3x12"] },
        { day: "Saturday", exercises: [" Shoulder Press - 4x8", " Lateral Raises - 3x12", " Front Raises - 3x12", " Rear Delt Flys - 3x15"] },
        { day: "Sunday", exercises: [" Active Recovery", "Stretching & Yoga"] }
      ]
    },
    fatLoss: {
      title: " Fat Loss",
      description: "Burn fat with high-intensity cardio and resistance workouts.",
      weeklyPlan: [
        { day: "Monday", exercises: [" HIIT Sprint Intervals - 20 mins", " Burpees - 3x15", " Kettlebell Swings - 3x20", " Jump Squats - 3x15", " Mountain Climbers - 3x30 sec"] },
        { day: "Tuesday", exercises: [" Deadlifts - 4x6", " Russian Twists - 3x20", " Push-ups - 4x12", " Side Planks - 3x30 sec per side"] },
        { day: "Wednesday", exercises: [" Running - 30 mins", " Cycling - 30 mins", " Core Work (Planks, Leg Raises)"] },
        { day: "Thursday", exercises: [" Jump Rope - 3x2 mins", " Burpees - 3x20", " HIIT Intervals - 30 sec sprints x 10", " Push-ups - 3x15"] },
        { day: "Friday", exercises: [" Kettlebell Swings - 3x20", " Deadlifts - 4x8", " High Knees - 3x30 sec", " Jump Squats - 3x15"] },
        { day: "Saturday", exercises: [" Squats - 4x10", " Mountain Climbers - 3x30 sec", " Sprint Intervals - 10x20 sec"] },
        { day: "Sunday", exercises: [" Active Recovery", "Yoga, Foam Rolling, Stretching"] }
      ]
    },
    strengthTraining: {
      title: " Strength Training",
      description: "Boost power and endurance with this heavy lifting plan.",
      weeklyPlan: [
        { day: "Monday", exercises: [" Deadlifts - 5x5", " Pull-ups - 4x8", " Squats - 5x5", " Overhead Press - 4x8", " Farmer's Walk - 3x30 sec"] },
        { day: "Tuesday", exercises: [" Barbell Rows - 4x8", " Bench Press - 4x6", " Dumbbell Shoulder Press - 4x10", " Triceps Pushdowns - 3x12"] },
        { day: "Wednesday", exercises: [" Cardio - 30 mins", " Stretching"] },
        { day: "Thursday", exercises: [" Squats - 5x5", " Romanian Deadlifts - 4x8", " Bulgarian Split Squats - 3x10", " Leg Curls - 3x10"] },
        { day: "Friday", exercises: [" Bench Press - 4x6", " Incline Dumbbell Press - 4x8", " Cable Chest Flys - 3x12", " Overhead Press - 4x8"] },
        { day: "Saturday", exercises: [" Bicep Curls - 3x12", " Deadlifts - 4x6", " Tricep Dips - 3x15", " Squats - 4x10"] },
        { day: "Sunday", exercises: [" Active Recovery", "Yoga & Stretching"] }
      ]
    }
  };

  return (
    <div className="workout-container">
      <h1> Workout Plans </h1>
      <p>Select the best workout plan tailored for your fitness goals.</p>

      <div className="workout-cards">
        {Object.keys(workoutPlans).map((key) => (
          <div key={key} className="workout-card" onClick={() => setSelectedWorkout(workoutPlans[key])}>
            <h2>{workoutPlans[key].title}</h2>
            <p>{workoutPlans[key].description}</p>
            <button>View Plan</button>
          </div>
        ))}
      </div>

      {selectedWorkout && (
        <div className={`popup ${selectedWorkout ? "active" : ""}`}>
          <div className="popup-content">
            <h2>{selectedWorkout.title}</h2>
            <p>{selectedWorkout.description}</p>
            <div className="weekly-plan">
              {selectedWorkout.weeklyPlan.map((dayPlan, index) => (
                <div key={index} className="day-card">
                  <h3>{dayPlan.day}</h3>
                  <ul>
                    {dayPlan.exercises.map((exercise, index) => (
                      <li key={index}>{exercise}</li>
                    ))}
                  </ul>
                </div>
              ))}
            </div>
            <button className="close-btn" onClick={() => setSelectedWorkout(null)}>
               Close
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default WorkoutPlans;
